<template>
    <div class="words-input">
        <label for="currentWord">
            {{ title }}

        <!-- Render the existing words as spans -->
        <span v-for="(word, index) in wordList" :key="index" class="word">
            {{ word }}
            <button class="remove-badge" @click="removeWord(index)">Ã—</button>
        </span>

        <!-- The last word is always an input field for typing -->
            <input id="currentWord"
            v-model="currentWord"
            class="last-word"
            type="text"
            list="completion-words"
            @keyup.space="addWord"
            @keyup.backspace="handleBackspace"
            @keyup.enter="submitWords"
            @input="handleCompletion"
            placeholder="Dodaj sÅ‚owo" />
        </label>

        <datalist id="completion-words">
        <option
            v-for="(word, index) in completion" :key="index"
            :value="word">{{ word }}
        </option>
        </datalist>

    </div>
</template>

<script>
export default {
  name: 'WordsInput',
  props: {
    title: { String },
    words: {
      type: Array,
      default: () => [],
    },
    completion: Array,
  },
  data() {
    return {
      wordList: [...this.words], // Local state of words
      currentWord: '', // The current word being typed
    };
  },
  watch: {
    words: {
      immediate: true,
      handler(newWords) {
        this.wordList = [...newWords];
      },
    },
  },
  methods: {
    addWord() {
      if (this.currentWord.trim()) {
        // Add current word to the list and reset input field
        this.wordList.push(this.currentWord.trim());
        this.currentWord = ''; // Reset the input field
        this.emitWords(); // Emit the updated list
      }
    },
    removeWord(index) {
      // Remove a word by index
      this.wordList.splice(index, 1);
      this.emitWords(); // Emit the updated list
    },
    handleBackspace() {
      if (this.currentWord === '' && this.wordList.length > 0) {
        // If input is empty and backspace is pressed, move the last word to the input
        this.currentWord = this.wordList.pop(); // Get the last word
        this.emitWords(); // Emit the updated list after removal
      }
    },
    emitWords() {
      // Emit the updated word list to parent component
      this.$emit('update:words', [...this.wordList]);
    },
    handleCompletion(e) {
      const selectedValue = e.target.value.trim();
      // Only finalize the word if the selected value exactly matches the current input,
      // and it was explicitly selected from the datalist via the change event.
      if (this.completion.includes(selectedValue) && e.inputType === undefined) {
        this.addWord(); // Treat it like they selected from autocomplete
      }
    },
    submitWords() {
      // Check if there's a current word and add it to the wordList if the input is not empty
      if (this.currentWord.trim()) {
        this.addWord(); // Call addWord to finalize the current word
      } else {
        this.emitWords(); // Emit the updated word list
      }
    },
  },
};
</script>

<style scoped>
.words-input {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
}

.word {
    padding: 5px 0px 5px 5px;
    margin-left: 2px;
    margin-right: 10px;
    background-color: #eee;
    border-radius: 10px;
    display: inline-flex;
    align-items: center;
}

.remove-badge {
    margin-left: 2px;
    margin-right: 2px;
    padding: 2px;
    border: 1px solid #eee;
    border-radius: 10px;
    cursor: pointer;
    color: red;
    font-weight: 700;
    position: relative;
    top: -7px;
    right: -2px;
    background-color: #d8d7d7;
    width: 20px;
    height: 20px;
    line-height: 0;
}

.last-word {
    min-width: 100px;
    padding: 5px;
    border: 1px solid #aaa;
    border-radius: 10px;
    margin-right: 5px;
}

input.last-word {
    margin: 5px 0;
}
</style>
