<template>
  <div class="container">
    <div class="card">
      <div class="card-header">
        <h3>Wyszukiwanie PieÅ›ni</h3>
        <WordsInput title="SÅ‚owa kluczowe" v-model:words="words" :completion="wordList()" />
        <div>
          <label for="tags" class="ms-2">
            PokaÅ¼ sÅ‚owa w wynikach:
            <input id="tags" type="checkbox" v-model="tags" />
          </label>
        </div>

        <div class="form-check form-switch">
          <label class="form-check-label d-block" for="liturg">
            <input class="form-check-input" type="checkbox"
            v-model="sectionLiturg" id="liturg">Liturgiczne
          </label>
          <label class="form-check-label d-block" for="precate">
            <input class="form-check-input" type="checkbox"
            v-model="sectionPrecate" id="precate">Prekatechumentu
          </label>
          <label class="form-check-label d-block" for="cate">
            <input class="form-check-input" type="checkbox"
            v-model="sectionCate" id="cate">Katechumentu
          </label>
          <label class="form-check-label d-block" for="elec">
            <input class="form-check-input" type="checkbox"
            v-model="sectionElec" id="elec">Wybrania
          </label>
        </div>
      </div>
      <div class="card-body">
        <SongList v-if="songs.length" :songs="selectedSongs(words)" :tags="tags" :words="words" />
      </div>
    </div>
  </div>

</template>

<script>
import SongList from '@/components/SongList.vue';
import WordsInput from '@/components/WordsInput.vue';

export default {
  name: 'HomeView',
  props: {
  },
  data() {
    return {
      songs: [],
      tags: true,
      words: [],
      sectionLiturg: false,
      sectionPrecate: true,
      sectionCate: true,
      sectionElec: true,
    };
  },
  computed: {
  },
  components: {
    SongList,
    WordsInput,
  },
  methods: {
    matchWords(song, words) {
      if (words.length) {
        return words.map((word) => {
          const lowWord = word.toLowerCase();
          return song.index.filter((songWord) => songWord.toLowerCase() === lowWord).length > 0;
        }).reduce((acc, v) => acc + v, 0);
      }
      return 0;
    },
    selectedSongs(words) {
      // prefilter by section, then by words
      const sections = [];
      if (this.sectionLiturg) sections.push('liturg');
      if (this.sectionPrecate) sections.push('precate');
      if (this.sectionCate) sections.push('cate');
      if (this.sectionElec) sections.push('elec');
      let songs = this.songs.filter((song) => (sections.includes(song.section)));

      if (words.length) {
        const rankedSongs = songs.map(
          (song) => ({ ...song, match: this.matchWords(song, words) }),
        );
        songs = rankedSongs.filter((song) => (song.match)).sort((a, b) => b.match - a.match);
      }
      return songs;
    },
    wordList() {
      if (this.songs) {
        const allWords = this.songs.map((song) => song.index).flat();
        return [...(new Set(allWords))];
      }
      return [];
    },
    async loadSongs() {
      let response;
      if (window.DecompressionStream) {
        console.log('Using DecompressionStream');
        const gunzip = new window.DecompressionStream('gzip');
        const gzResponse = await fetch('songs.json.gz');
        const blob = await gzResponse.blob();
        const decompressedStream = blob.stream().pipeThrough(gunzip);
        response = new window.Response(decompressedStream);
      } else {
        console.log('No support for DecompressionStream');
        response = await fetch('songs.json');
      }
      this.songs = await response.json();
      console.log('Loaded songs');
    },
  },
  async mounted() {
    await this.loadSongs();
  },
};
</script>
